<script>
import ModelViewer from '@/components/ModelViewer.vue'

export default {
    components: {
        ModelViewer
    },
    data() {
        return {
            activeIndex2: '1',
            userId: null,
            buildingId: null,
            buildName: '楼宇一',
            buildAddress: '中国山东济南山东建筑大学',
            deviceNum: '1000',
            electMonth: '12224.2',
            waterMonth: '24651.3',
            waterYear: '246554.3',
            electYear: '146115.3',
            activeButton: 1,
            tpMonth: '0.0',
            tpYear: '0.0',
            assessmentYear: '2023',
            energyBaseline: '85.6',
            annualElectricity: '146115.3',
            energySaving: '12450.8',
            modelPath: '/ESP/aa.obj' // 从 public 目录加载OBJ模型
        }
    },
    watch: {
        // 监听路由变化，当buildingId改变时重新加载数据
        '$route.query.buildingId': {
            handler(newBuildingId) {
                if (newBuildingId) {
                    this.buildingId = newBuildingId;
                    this.loadBuildingData(newBuildingId);
                }
            },
            immediate: true
        }
    },
    mounted() {
        // 获取路由传递的参数
        this.userId = this.$route.query.userId;
        this.buildingId = this.$route.query.buildingId;

        // 可以根据userId和buildingId从后端获取具体的数据
        console.log('用户ID:', this.userId);
        console.log('楼宇ID:', this.buildingId);

        // 确保在首次加载时也调用数据加载方法
        if (this.buildingId) {
            this.loadBuildingData(this.buildingId);
        }
    },
    methods: {
        loadBuildingData(buildingId) {
            // 根据楼宇ID加载楼宇数据
            // 这里应该调用API获取具体楼宇信息
            switch (buildingId) {
                case '1':
                    this.buildName = '楼宇A';
                    this.buildAddress = '中国山东济南山东建筑大学A座';
                    this.deviceNum = '1200';
                    this.electMonth = '15624.5';
                    this.waterMonth = '26541.2';
                    break;
                case '2':
                    this.buildName = '楼宇B';
                    this.buildAddress = '中国山东济南山东建筑大学B座';
                    this.deviceNum = '800';
                    this.electMonth = '10124.3';
                    this.waterMonth = '21651.7';
                    break;
                case '3':
                    this.buildName = '楼宇C';
                    this.buildAddress = '中国山东济南山东建筑大学C座';
                    this.deviceNum = '1500';
                    this.electMonth = '18424.8';
                    this.waterMonth = '31651.1';
                    break;
                default:
                    this.buildName = '默认楼宇';
                    this.buildAddress = '中国山东济南山东建筑大学AAAA';
                    this.deviceNum = '1000';
                    this.electMonth = '12224.2';
                    this.waterMonth = '24651.3';
            }

            // 模拟从后端获取其他数据
            // 实际项目中应该调用API获取真实数据
            console.log(`加载楼宇 ${buildingId} 的数据`);
        }
    }
}
</script>


